<template>
  <el-form class="form" :inline="true" @submit.native.prevent="onSubmit" :model="form" label-width="100px">
    <el-breadcrumb separator="/" style="margin-bottom: 30px;">
      <el-breadcrumb-item to="/">工作台</el-breadcrumb-item>
      <el-breadcrumb-item to="/article/index">文章列表</el-breadcrumb-item>
      <el-breadcrumb-item>详情</el-breadcrumb-item>
    </el-breadcrumb>
    <el-row :gutter="24">
      <el-col>
        <el-form-item label="文章标题">
          <el-input v-model="form.title" placeholder="请输入商品标题"></el-input>
          <el-input v-model="form.title_en" placeholder="请输入文章标题(英文)"></el-input>
          <el-input v-model="form.title_yn" placeholder="请输入文章标题(越南)"></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="文章排序">
          <el-input type="number" v-model="form.sort" placeholder="请输入文章排序"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="24">
        <el-form-item label="设置发布日期">
          <el-date-picker
            v-model="form.pubtime"
            type="date"
            placeholder="选择日期"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy-MM-dd"
          >
          </el-date-picker>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="是否首页显示">
          <el-select size="small" v-model="form.is_show_index" clearable placeholder="请选择">
            <el-option value="Y" label="是" />
            <el-option value="N" label="否" />
          </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="24">
        <el-form-item label="封面" prop="thumb">
          <el-upload
            :limit="1"
            :headers="myHeaders"
            :action="'/v1/api/api/api/upload'"
            :on-success="fileSuccess1"
            :before-upload="fileSuccessBefore"
            :file-list="file"
            list-type="picture"
            :on-remove="onDelct1"
          >
            <el-button size="small" type="infor">点击上传</el-button>
          </el-upload>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="内容" style="display: flex;">
          <vue-editor
            useCustomImageHandler
            @image-added="handleImageAdded"
            v-model="form.content"
            style="flex: 1;"
          ></vue-editor>
        </el-form-item>
        <el-form-item label="内容(英文)" style="display: flex;">
          <vue-editor
            useCustomImageHandler
            @image-added="handleImageAdded"
            v-model="form.content_en"
            style="flex: 1;"
          ></vue-editor>
        </el-form-item>
        <el-form-item label="内容（越南）" style="display: flex;">
          <vue-editor
            useCustomImageHandler
            @image-added="handleImageAdded"
            v-model="form.content_yn"
            style="flex: 1;"
          ></vue-editor>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item>
          <el-button type="primary" native-type="submit">保存</el-button>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>
<script>
import Fetch from '../../utils/fetch'
import Fetch2 from '../../utils/fetch2'
import Fetch3 from '../../utils/fetch3'
import Fetch4 from '../../utils/fetch4'
import { VueEditor } from 'vue2-editor'
import { Message } from 'element-ui'

export default {
  components: {
    VueEditor,
  },
  data() {
    return {
      myHeaders: { Authorization: 'Bearer ' + localStorage.getItem('pstt') },
      config: {
        cats: {},
      },
      file: [],
      form: {
        title: '',
        title_en: '',
        title_yn: '',
        content: '',
        content_en: '',
        content_yn: '',
        cid: 5,
        is_show_index: 'Y',
        sort: 0,
        type: 'notice',
        pubtime: '',
      },
    }
  },
  computed: {},
  mounted() {
    const id = this.$router.history.current.params.id

    Fetch('/article/art/config', { id }).then(r => {
      this.config.cats = r.data.cats
    })

    if (id > 0) {
      Fetch2('/article/art/edit/' + id).then(r => {
        this.file = [{ url: r.data.view.thumb, name: r.data.view.thumb }]
        this.form = r.data.view
      })
    }
  },
  methods: {
    fileSuccess1(res, file) {
      this.file = [file]
      this.form.thumb = res.data.file
    },
    fileSuccessBefore(file) {
      const uploadLimit = 2
      const filetype = file.name.replace(/.+\./, '')
      const uploadTypes = ['png', 'jpg', 'jpeg', 'gif']
      const isRightSize = (file.size || 0) / 1024 / 1024 < uploadLimit
      if (!isRightSize) {
        this.$message.error('文件大小超过 ' + uploadLimit + 'MB')
        return false
      }
      if (uploadTypes.indexOf(filetype.toLowerCase()) === -1) {
        this.$message.warning({
          message: '请上传后缀名为png、jpg、jpeg、gif的附件',
        })
        return false
      }
      return true
    },

    onDelct1(file, fileList) {
      this.file1 = fileList
      this.form.thumb = fileList.map(item => item.name)
    },
    handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      const formData = new FormData()
      console.log(formData)
      formData.append('file', file)
      Fetch('/api/api/upload', formData).then(result => {
        let url = result.data.file
        Editor.insertEmbed(cursorLocation, 'image', url)
        resetUploader()
      })
    },
    onSubmit() {
      const id = this.$router.history.current.params.id
      if (typeof id != 'undefined') {
        Fetch3('/article/art/edit/' + id, {
          id: this.$router.history.current.params.id,
          ...this.form,
        }).then(() => {
          Message.success('操作成功')
          this.$router.replace('/article/noticelist')
        })
      } else {
        Fetch('/article/art/create', {
          id: this.$router.history.current.params.id,
          ...this.form,
        }).then(() => {
          Message.success('操作成功')
          this.$router.replace('/article/noticelist')
        })
      }
    },
  },
}
</script>
<style lang="less" scoped>
.form {
  /deep/ .el-input__inner {
    width: 300px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
</style>
